import { Component, OnInit, ViewChild } from '@angular/core';
import { PersonComponent } from '../person/person.component';

@Component({
  selector: 'app-listperson',
  templateUrl: './listperson.component.html',
  styleUrls: ['./listperson.component.css']
})
export class ListpersonComponent implements OnInit {
	arrPerson = [
	{id:'12345', name:'Trần Hoàng Phúc', age : 21},
	{id:'12481', name:'Hồ Thanh Điền', age : 20},
	{id:'32481', name:'Trần Hoàng Huy', age : 15},
	];
  constructor() { }

  ngOnInit() {
  }
  value:number = 0;
  addValue(addOrSub:boolean){
  	addOrSub ? this.value = this.value + 1 : this.value = this.value - 1;
  }
  //ViewChild
  @ViewChild(PersonComponent)
  myChild:PersonComponent;
  clickAdd(){
  	this.myChild.value++;
  }
}



