import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule} from  '@angular/forms';
import {HttpModule} from '@angular/http';
import { AppComponent } from './app.component';
import {HoangPhucComponent} from './Component/Me/me.component';
import { LaptopComponent } from './Component/laptop/laptop.component';
import { TinhLuongComponent } from './Component/tinh-luong/tinh-luong.component';
import { PersonComponent } from './Component/person/person.component';
import { ListpersonComponent } from './Component/listperson/listperson.component';
import { ContentComponent } from './Component/content/content.component';
import { PipeComponent } from './Component/pipe/pipe.component';
import { CalculationComponentPipe} from './Component/app.componentpipe';
import { ServiceComponent } from './Component/service/service.component';
import { IpService } from './Component/ip.service';
import { WeatherComponent } from './Component/weather/weather.component';
import { RouterComponent } from './Component/router/router.component';
import { AppRoutingModule} from './Component/app-routing.module';


@NgModule({
  declarations: [
    AppComponent,
    HoangPhucComponent,
    LaptopComponent,
    TinhLuongComponent,
    PersonComponent,
    ListpersonComponent,
    ContentComponent,
    PipeComponent,
    CalculationComponentPipe,
    ServiceComponent,
    WeatherComponent,
    RouterComponent
    
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule
  ],
  providers: [IpService],//nhiều component sử dụng 1 service
  bootstrap: [AppComponent]
})
export class AppModule { }



