import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-tudien',
  templateUrl: './tudien.component.html',
  styleUrls: ['./tudien.component.css']
})
export class TudienComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }


  arrayWords = [
  {id:1, en:'animals', vn:'động vật', memoried:true},
  {id:2, en:'tiger', vn:'con hổ', memoried:true},
  {id:3, en:'lion', vn:'sư tử', memoried:true},
  {id:4, en:'dog', vn:'chó', memoried:false},
  {id:5, en:'duck', vn:'vịt', memoried:false},
  {id:6, en:'mouse', vn:'chuột', memoried:true},
  {id:7, en:'bird', vn:'chim', memoried:true},
  {id:8, en:'snake', vn:'rắn', memoried:true},
  {id:9, en:'fish', vn:'cá', memoried:false},
  {id:10, en:'cat', vn:'mèo', memoried:false}
  ]
  status='XEM_TAT_CA';
  newEng:string = ' ';
  newVn:string = ' ';
  showForm:boolean = false;
  add(){
  		this.arrayWords.push(
  		{id : this.arrayWords.length + 1, en:this.newEng, vn : this.newVn, memoried:false}
  		)
  		this.newEng = ' ';
  		this.newVn = ' ';
  		this.showForm = false;
  }
  delete(id:number){
   
  		const index = this.arrayWords.findIndex(word => word.id === id);
  		this.arrayWords.splice(index,1);
  }
  getStatus(memoried:boolean){
  		const xemTatCa = this.status === 'XEM_TAT_CA';
  		const xemDaHoc = this.status === 'XEM_DA_HOC' && !memoried;
  		const xemChuaHoc = this.status === 'XEM_CHUA_HOC' && memoried;
  		return xemTatCa || xemDaHoc || xemChuaHoc;
  }
}



