import { Component, OnInit, Input} from '@angular/core';

@Component({
  selector: 'app-laptop',
  templateUrl: './laptop.component.html',
  styleUrls: ['./laptop.component.css']
})
export class LaptopComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }
newEng:string = " ";
newVn:string = " ";
isTruly:boolean=true;
name:string = " " ;
htd = { color : 'green', fontSize : '50px'};
//thp = (!this.isTruly ? 'square' : 'circle');
thpp = {square : this.isTruly, circle : !this.isTruly};
isP:boolean = false;
theface = ["SamSung", "OPPO", "Apple"];
dic = [
{id :1, en: 'go' , vn:'đi', memoried : true},
{id :2, en: 'eat' , vn:'ăn', memoried : true},
{id :3, en: 'drink' , vn:'uống', memoried : false}
];
addDic = {id :10, en: 'mouse' , vn:'chuột', memoried : true};
add(){
	this.dic.unshift({
		id : this.dic.length + 1,
		en : this.newEng,
		vn : this.newVn,
		memoried : true
	}
		)
	this.newEng ='';
	this.newVn ='';
}
@Input() id:string;
@Input() namem:string;
@Input() age:number;

}



