import { NgModule } from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { CommonModule } from '@angular/common'; 
import {PagenotfoundComponent} from './pagenotfound/pagenotfound.component';
import { TudienComponent } from './tudien/tudien.component';
import { FormComponent } from './form/form.component';
import {FormsModule} from  '@angular/forms';
import {ContactModule} from './contact/contact.module'
import { DetailcontactComponent } from './detailcontact/detailcontact.component'

const routesConfig: Routes = [
{path : 'tudien', component : TudienComponent},
{path : 'form', component : FormComponent},
// {path : '', redirectTo : '/tudien', pathMatch : 'full'},//mặc định khi chạy lên sẽ hiển thị component này
// {path : '**', component : PagenotfoundComponent},// khi người dùng nhập vào 1 trang k tồn tại
{path : 'detailcontact/:id/:name/:phone', component:DetailcontactComponent},

];

@NgModule({
	imports  : [
	ContactModule,
	RouterModule.forRoot(routesConfig),//truyền vào routesConfig
	CommonModule,
	 FormsModule
	],
	declarations:[
	 TudienComponent,
	 FormComponent,
	 PagenotfoundComponent,
     DetailcontactComponent
	 ],
	 exports : [RouterModule]
})
export class AppRoutingModule {}


